import { Link, useNavigate } from 'react-router-dom'
import React, { useState, useEffect } from 'react'
import DataTable from 'react-data-table-component'
import Cookies from 'universal-cookie'
import ClipLoader from 'react-spinners/ClipLoader'
const axios = require('axios');

const RewardOptionList = ({showLoading}) => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(true)
  const [data, setData] = useState([])
  const [columns, setColumns] = useState([
    {
      name: 'Option',
      selector: row => row.option,
      sortable: true,
    },
    {
      name: 'Description',
      selector: row => row.description,
      sortable: true,
    },
    {
      name: 'Suboption',
      cell: row => <Link className="btn btn-primary" to={`/reward/${row.id}`}>View</Link>,
    },
    {
      name: 'Action',
      cell: (row) => <>
      <div className="text-nowrap">
        <button className="btn btn-warning me-3" onClick={() => navigate(`/reward/edit/${row.id}`)}>Edit</button>
        <button className="btn btn-danger" onClick={() => deleteRewardOption(row.id, setIsLoading)}>Delete</button>
      </div>
      </>,
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    }
  ])

  useEffect(() => {
    if (!localStorage.getItem('token')) {
      navigate('/login');
    }
    getRewardOptions();
  }, []);

  const getRewardOptions = async () => {
    const response = await axios.get('http://wongdeveloper.uk/api/v1/rewards/');
    setData(response.data.data);
    setIsLoading(false);
  }

  const deleteRewardOption = async (id, setIsLoading) => {
    setIsLoading(true);
    await axios.delete(`http://wongdeveloper.uk/api/v1/rewards/${id}/`).then((res) => {
      setIsLoading(false)
      return res
    }).catch((err) => {
      setIsLoading(false)
      return err
    })
    getRewardOptions();
  }

  return (
    <>
    <div className="container mb-4">
      <a href="/">Home</a> / <a>Reward</a>
    </div>
    <div>
      <div className="row justify-content-between align-items-center">
        <h1 className="col-3">Reward Option</h1>
        <button className="col-2 btn btn-primary h4 mb-0" onClick={() => navigate('/reward/create')}>Add Reward</button>
      </div>  
      <DataTable columns={columns} data={data} progressPending={isLoading} />
    </div>
    </>
  );
}

export default RewardOptionList;
